.narahosting-cta-block {
	display: flex;
	flex-wrap: inherit;

	/* Allows wrapping on smaller screens */
	align-items: center;
	justify-content: center;
	gap: 1rem;
}

.narahosting-cta-subheading {
	font-size: 17px;
	color: #F87060;
}

.narahosting-cta-content {
	flex: 1;
	min-width: 250px;
	max-width: 21rem;
}

.narahosting-cta-image {
	max-width: 300px;

	/* Adjust as needed */
	img {
		width: 100%;
		height: auto;
		display: block;
	}
}

.narahosting-cta-button {
	display: inline-block;
	margin-top: 1rem;
}